<template>
    <div id="section1">
        <div id="bg"></div>
        <div id="menu-container">
            <div id="menu"></div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'section1'
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    #section1{
        width:100%;
        height:100%;
        position:relative;
    }
    #bg{
        width:100%;
        height:100%;
        background: black;
        opacity: 0.4;
    }
    #menu-container{
        position:absolute;
        width:500px;
        height:500px;
        top:20%;
        left:0;
        right:0;
        background: white;
        border-radius:50%;
        margin:0px auto;
    }
    #menu{
        top:0px;
        background: white;
        border-radius:50%;
        margin:0px auto;
    }
</style>



